@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,300,600,700);
svg.loading{width:150px;height:150px;top:50%;left:50%;margin:-75px 0 0 -75px;position:absolute}svg.loading path:nth-of-type(1){fill:#f5b697}svg.loading path:nth-of-type(2){fill:#f5e797}svg.loading path:nth-of-type(3){fill:#f29c98}svg.loading path:nth-of-type(4){fill:#f298bf}

nav.siteNav ul{margin:0;border-bottom:4px solid #f7f7f7;text-align:right}nav.siteNav ul li{display:inline-block;padding:12px}nav.siteNav ul li a{color:#333}

body{margin:0;font-family:"Open Sans", Helvetica, Arial;background-color:#f7f7f7;color:#333}body a{text-decoration:none}body #root{width:80%;max-width:900px;margin:0 auto;position:relative;background-color:#fff;padding:36px}body #root .app footer{text-align:center;margin-top:36px}body #root .app footer svg{fill:#777}body #root .app footer svg:hover{fill:#000}body #root .app .coinTabs{text-align:center;list-style-type:none;padding-left:0}body #root .app .coinTabs li{display:inline-block;padding:12px;cursor:pointer;min-width:50px}body #root .app .coinTabs li.selected{font-weight:bold}

